$(function() {
    $(".header__bars").click(function() {
        $('.aside__menu').addClass("active-menu");
    });


    $(".aside-close__btn").click(function() {
        $('.aside__menu').removeClass("active-menu");
    });

    // js slider in index.html 
    $('.slider').slick({
            dots: true,
            aroows: true,
            autoplay: true,
            fade: true,
            cssEase: 'linear',
            autoplaySpeed: 2000
        })
        // js slider in index.html
    $('.slider__box').slick({
        dots: true,
        slidesToShow: 1,
        autoplay: true,
        autoplaySpeed: 2000,
        variableWidth: true
    })
});